<template>
  <div class="intruduece">
    <div class="content">
      <transition name="des">
        <div class="contmain" v-if="trans">
          <div class="logo">
            <div class="left-logo">
              <img src="../assets/about/logo.png" alt />
            </div>
            <div class="right-about">
              <p class="cname"></p>
              <p class="ename">About Longlake</p>
            </div>
          </div>
          <div class="des">
            <vue-scroll :ops="ops" style="height:445px">
              <p>
                () 
                 10200
                2002501050800
                 ******
                
                
                :
                :
                ****
                
                
              </p>
            </vue-scroll>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: "about",
  data() {
    return {
      transa: false,
      trans: false,
      videoPlay: false,
      ops: {
        vuescroll: {
          mode: "native",
          sizeStrategy: "percent",
          detectResize: true
        },
        scrollPanel: {
          scrollingY: true,
          speed: 1000
        },
        rail: {
          background: "#090909",
          opacity: 0,
          size: "5px",
          specifyBorderRadius: false,
          gutterOfEnds: "4%", // x  y 
          gutterOfSide: "1%", //
          keepShow: false, // bar 
          border: "none" //
        },
        bar: {
          minSize: 0.3,
          hoverStyle: true,
          onlyShowBarOnScroll: false, //
          background: "#82c41c" //
        }
      }
    };
  },
  mounted() {
    this.trans = true;
    this.transa = true;
  },
  methods: {
    play() {
      this.videoPlay = true;
    },
    close() {
      this.videoPlay = false;
    }
  }
};
</script>
<style lang="less" scoped>
// 
// .logo-enter-active,
// .right-enter-active {
//   transition: all 1.5s ease-in-out;
// }
// .logo-enter {
//   transform: translateX(-50px);
// }
// .right-enter {
//   transform: translateX(100px);
// }
.des-enter-active {
  transition: all 1.5s ease-in-out;
}
.des-enter {
  transform: translateY(150px);
}

.intruduece {
  width: 100%;
  position: relative;
  box-sizing: border-box;
  .content {
    width: 100%;
    padding: 160px 0 70px 0;
    .contmain {
      width: calc(100% - 50px);
      margin: 0 auto;
      padding-bottom: 25px;
      border: 10px solid #939393;
      .logo {
        padding: 30px 40px;
        text-align: right;
        display: flex;
        justify-content: space-between;
        .right-about {
          .cname {
            font-size: 26px;
            color: #ffffff;
            position: relative;
            &::after {
              content: "";
              width: 220px;
              height: 4px;
              background: #82c41c;
              position: absolute;
              top: 60px;
              right: 0;
            }
          }
          .ename {
            padding-top: 50px;
            font-size: 22px;
            color: #bbbbbb;
          }
        }
      }
      .des {
        box-sizing: border-box;

        p {
          box-sizing: border-box;
          margin: 0px auto;
          color: white;
          opacity: 0.8;
          font-size: 14px;
          padding: 0 40px;
          white-space: pre-wrap;
          white-space: pre-line;
          word-break: break-all;
        }
      }
    }
  }
}
</style>


